
import { defineComponent } from "vue";

export default defineComponent({
  name: "IgToggleBtn",
  props: {
    changeToggleOption: {
      type: Function,
    },
  },
  data() {
    return {
      activeToggle: false,
    };
  },
  methods: {
    changeOption() {
      this.activeToggle = !this.activeToggle;
      this.$emit("changeToggleOption", this.activeToggle);
    },
  },
});
