<template>
  <a href="javascript:void(0)" @click="changeOption()">
    <div class="toggle-btn" :class="activeToggle ? 'active' : ''">
      <div class="icon">
        <svg
          width="0.6rem"
          height="0.6rem"
          viewBox="0 0 8 8"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fill-rule="evenodd"
            clip-rule="evenodd"
            d="M1.75 0C1.05964 0 0.5 0.559644 0.5 1.25V6.75C0.5 7.44036 1.05964 8 1.75 8C2.44036 8 3 7.44036 3 6.75V1.25C3 0.559644 2.44036 0 1.75 0ZM6.25 0C5.55964 0 5 0.559644 5 1.25V6.75C5 7.44036 5.55964 8 6.25 8C6.94036 8 7.5 7.44036 7.5 6.75V1.25C7.5 0.559644 6.94036 0 6.25 0Z"
            :fill="activeToggle ? 'white' : 'gray'"
          />
        </svg>
      </div>
    </div>
  </a>
</template>
<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  name: "IgToggleBtn",
  props: {
    changeToggleOption: {
      type: Function,
    },
  },
  data() {
    return {
      activeToggle: false,
    };
  },
  methods: {
    changeOption() {
      this.activeToggle = !this.activeToggle;
      this.$emit("changeToggleOption", this.activeToggle);
    },
  },
});
</script>
<style lang="scss" scoped>
.toggle-btn {
  width: 3rem;
  height: 1rem;
  position: relative;
  background-color: #1c1f27;
  border-radius: 15px;
  .icon {
    left: 0rem;
    top: -0.3rem;
    position: absolute;
    width: 1.6rem;
    height: 1.6rem;
    display: flex;
    border-radius: 50%;
    background: #282832;
    transition: left 200ms;

    svg {
      margin: auto;
    }
  }
  &.active {
    .icon {
      background: $twPurple;
      left: 1.4rem;
    }
  }
}
</style>
