<script lang="ts">
import { defineComponent } from "vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import IgToggleBtn from "@/components/baseUI/IgToggleBtn.vue";

import { Menu, MenuButton, MenuItems, MenuItem } from "@headlessui/vue";
import GameList from "@/components/gameList/GameList.vue";
import axios from "axios";
import { mapState } from "vuex";
export default defineComponent({
  data() {
    return {
      keyword: "",
      applyFilter: false,
      filterData: [
        {
          title: "Game type",
          key: "game_type",
          tags: [
            { name: "Adventour", value: "adventour", selected: false },
            { name: "Racing", value: "racing", selected: false },
            { name: "Loren", value: "loren", selected: false },
            { name: "Lorem ipsum", value: "loren_ipsum", selected: false },
            { name: "Lorem ipsum", value: "loren_ipsum", selected: false },
          ],
        },
        {
          title: "Number of people playing",
          key: "number_of_people_playing",
          tags: [
            { name: "1 only", value: "1", selected: false },
            { name: "1 - 5 only", value: "1_to_5", selected: false },
            { name: "5 people only", value: "5", selected: false },
          ],
        },
      ],

      currentlyAvailable: false,

      gamePage: 1,
      gameTotal: 0,
      gameData: [],
    };
  },
  components: {
    IgButton,
    Menu,
    MenuButton,
    MenuItems,
    MenuItem,
    GameList,
    IgToggleBtn,
  },
  computed: {
    ...mapState(["apiUrl"]),
    checkFilterApply() {
      for (let i = 0; i < this.filterData.length; i++) {
        const item = this.filterData[i];
        for (let j = 0; j < item.tags.length; j++) {
          const el = item.tags[j];
          if (el.selected) {
            return true;
          }
        }
      }
      return false;
    },
  },
  methods: {
    async getGame(page) {
      var url =
        this.apiUrl +
        "/games/?page=" +
        page +
        "&keyword=" +
        this.keyword +
        "&tournament_currently_available=" +
        this.currentlyAvailable;

      var response = await axios.get(url);
      if (response.data) {
        this.gamePage = page;
        this.gameTotal = response.data.count;
        if (page == 1) this.gameData = [];
        this.gameData = this.gameData.concat(response.data.results);
      }
    },
    changeCurrentlyAvailable(x) {
      this.currentlyAvailable = x;
      this.getGame(1);
    },
    getSearchData() {
      if (this.keyword.length % 3 == 0) {
        this.getGame(1);
      }
    },
    cleanSearchData() {
      this.keyword = "";
      this.getGame(1);
    },
  },
  async created() {
    await this.getGame(1);
  },
});
</script>
